<template>
  <div class="storage">
    <vTable
      :caption="table.caption"
      :columns="table.columns"
      :data="storage"
      :operations="table.operations"
      @onDelete="deleteStorageItem"
      @onCount="count"
    />
  </div>
</template>

<script>
import {mapActions} from 'vuex'

import vTable from '@/components/table/v-table'
import vTableColumn from '@/components/table/v-table-column'

import {dateNow} from '@/helpers'

export default {
  name: 'Storage',
  components: {
    vTable,
    vTableColumn
  },
  data: () => ({
    table: {
      caption: 'Таблица склада',
      operations: {
        count: true,
        delete: true,
      },
      columns: [
        'Дата',
        'Название',
        'Кол-во',
        'Себестоимость',
        'Выручка',
      ]
    }
  }),
  computed: {
    storage() {
      return this.$store.getters.STORAGE.map(i => ({
        ...i,
        costPrice: i.costPrice * i.quantity,
        revenue: i.revenue * i.quantity
      }))
    }
  },
  methods: {
    ...mapActions(['DELETE_STORAGE_ITEM', 'ADD_SALE_ITEM', 'CHANGE_STORAGE_ITEM']),
    deleteStorageItem(id) {
      this.DELETE_STORAGE_ITEM(id)
    },
    count(item) {
      const numberSold = +prompt('Кол-во проданных товаров:')

      const saleItem = {
        id: Date.now(),
        date: dateNow(),
        name: item.name,
        quantity: numberSold,
        proceeds: item.revenue / item.quantity * numberSold
      }

      let changeStorageItem = {
        ...item,
        quantity: item.quantity - numberSold,
        costPrice: (item.costPrice / item.quantity),
        revenue: (item.revenue / item.quantity),
      }
      this.ADD_SALE_ITEM(saleItem)
      this.CHANGE_STORAGE_ITEM(changeStorageItem)
    }
  }
}
</script>
