<template>
  <div class="dish">
    <div class="dish__form">
      <input v-model="name" type="text" placeholder="Название">
      <input v-model="price" type="number" placeholder="Цена продажи">
      <button @click="addDish">Добавить</button>
    </div>
    <div class="dish__products">
      <h3 class="dish__products-title">Выбранные продуты:</h3>
      <ul class="dish__products-list">
        <li
          v-for="(p, idx) in selectProducts"
          :key="idx"
        >
          {{p.name}} ({{p.amount}})
          <i
            class="material-icons delete" 
            title="Удалить"
            @click="deleteSelectProduct(idx)"
          >
            delete
          </i>
        </li>
      </ul>
      <b>Себестоимость: {{costPrice}}</b>
    </div>
    <vTable
      :caption="table.caption"
      :columns="table.columns"
      :data="PRODUCTS"
      :operations="table.operations"
      @onAdd="addSelectItem"
    />
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

import vTable from '@/components/table/v-table'
import vTableColumn from '@/components/table/v-table-column'

export default {
  name: 'Dish',
  components: {
    vTable,
    vTableColumn
  },
  data: () => ({
    name: '',
    price: 0,
    costPrice: 0,
    table: {
      caption: 'Таблица продуктов',
      operations: {
        add: true
      },
      columns: [
        'Название',
        'Масса',
        'Себестоимость',
      ]
    },
    selectProducts: []
  }),
  
  computed: {
    ...mapGetters(['PRODUCTS'])
  },
  
  methods: {
    ...mapActions(['ADD_DISH_ITEM']),
    deleteSelectProduct(id) {
      this.selectProducts = this.selectProducts.filter((p, idx) => idx !== id)
      // Подсчет цены
      this.countPrice()
    },
    addSelectItem(i) {
      const item = {...i}
      const productIdx = this.selectProducts.findIndex(p => p.name === item.name)
      if (productIdx !== -1) {
        this.selectProducts[productIdx].amount++
      }
      else {
        this.$set(item, 'amount', 1)
        this.selectProducts.push(item)
      }
      // Подсчет цены
      this.countPrice()
    },
    addDish() {
      let dish = {
        id: Date.now(),
        name: this.name,
        products: this.selectProducts,
        costPrice: this.costPrice,
        price: this.price
      }

      this.ADD_DISH_ITEM(dish)
      this.name = ''
      this.selectProducts = []
      this.costPrice = 0
      this.price = 0
    },
    countPrice() {
      this.costPrice = this.selectProducts.reduce((total, p) => total += p.costPrice * p.amount, 0)
    }
  }
}
</script>

