<template>
  <tr class="v-table__row">
    <td 
      v-for="(cell, idx) in cells"
      :key="idx"
    >{{cell}}</td>
    <td 
      class="v-table__operations"
      v-if="operations"
    >
      <i @click="count(rowData)" class="material-icons count" title="Подсчитать" v-if="operations.count">attach_money</i>
      <i @click="deleteItem(rowData.id)" class="material-icons delete" title="Удалить" v-if="operations.delete">delete</i>
      <i @click="addItem(rowData)" class="material-icons add" title="Добавить" v-if="operations.add">add</i>
    </td>
  </tr>
</template>

<script>
export default {
  name: 'v-table-item',
  props: {
    rowData: Object,
    operations: Object,
  },
  computed: {
    cells: function() {
      let cells = {...this.rowData}
      delete cells.id
      return cells
    }
  },
  methods: {
    deleteItem(id) {
      this.$emit('onDelete', id)
    },
    addItem(item) {
      this.$emit('onAdd', item)
    },
    count(item) {
      this.$emit('onCount', item)
    }
  }
}
</script>

