import Vue from 'vue'
import VueRouter from 'vue-router'
import Storage from '../views/Storage.vue'
import Products from '../views/Products.vue'
import Dish from '../views/Dish'
import Sales from '../views/Sales'
import AddToStorage from '../views/AddToStorage'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Storage',
    component: Storage
  },
  {
    path: '/products',
    name: 'Products',
    component: Products
  },
  {
    path: '/dish',
    name: 'Dish',
    component: Dish
  },
  {
    path: '/sales',
    name: 'Sales',
    component: Sales
  },
  {
    path: '/addToStorage',
    name: 'AddToStorage',
    component: AddToStorage
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
