<template>
  <div class="products">
    <div class="products__form">
      <input v-model="product.name" type="text" placeholder="Название">
      <input v-model="product.weight" type="number" placeholder="Масса">
      <input v-model="product.costPrice" type="number" placeholder="Себестоимость">
      <button @click="addProduct">Добавить</button>
    </div>
    <vTable
      :caption="table.caption"
      :columns="table.columns"
      :data="PRODUCTS"
      :operations="table.operations"
      @onDelete="deleteProductItem"
    />
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

import vTable from '@/components/table/v-table'
import vTableColumn from '@/components/table/v-table-column'

export default {
  name: 'Products',
  components: {
    vTable,
    vTableColumn
  },
  data: () => ({
    table: {
      caption: 'Таблица продуктов',
      operations: {
        delete: true
      },
      columns: [
        'Название',
        'Масса',
        'Себестоимость',
      ]
    },

    product: {
      name: '',
      weight: '',
      costPrice: ''
    }
  }),

  computed: {
    ...mapGetters(['PRODUCTS'])
  },
  
  methods: {
    ...mapActions(['ADD_PRODUCT_ITEM', 'DELETE_PRODUCT_ITEM']),
    addProduct() {
      // this.table.data.push(this.product)
      this.ADD_PRODUCT_ITEM(this.product)

      this.product = {
        name: '',
        weight: '',
        costPrice: ''
      }
    },
    deleteProductItem(id) {
      this.DELETE_PRODUCT_ITEM(id)
      // this.$set(this.table, 'data', this.table.data.filter((el, idx) => idx !== id))
    }
  }
}
</script>

<style lang="scss">
  .products {
    &__form {
      margin-bottom: 2rem;
      input, button {
        padding: .5rem 2rem;
      }
      input {
        margin-right: .5rem;
      }
      button {
        cursor: pointer;
      }
    }
  }
</style>
