<template>
  <div class="v-table-wrapper">
    <div class="v-table__top">
      <caption class="v-table__title">
        {{caption}}
      </caption>
      <input 
        class="v-table__search" 
        type="text" 
        placeholder="Поиск"
        v-model="search"
      >
    </div>
    <table class="v-table">
      <tr class="v-table__head v-table__row">
        <vTableColumn
          v-for="(column, idx) in columns"
          :key="idx"
          :label="column"
        />

        <vTableColumn 
          v-if="operations"
          label="Операции"
        />      
      </tr>
      <vTableItem 
        class="v-table__row"
        v-for="row in filterData"
        :key="row.id"
        :rowData="row"
        :operations="operations"
        @onDelete="deleteItem"
        @onAdd="addItem"
        @onCount="count"
      />
    </table>
  </div>
</template>

<script>
import vTableColumn from './v-table-column'
import vTableItem from './v-tabel-item'

export default {
  name: 'v-table',
  components: {
    vTableColumn,
    vTableItem
  },
  data: () => ({
    search: ''
  }),
  props: {
    caption: String,
    operations: Object,
    columns: Array,
    data: Array
  },
  computed: {
    filterData: function() {
      return this.data.filter(el => el.name.toLowerCase().indexOf(this.search.toLowerCase()) !== -1)
    }
  },
  methods: {
    deleteItem(id) {
      this.$emit('onDelete', id)
    },
    addItem(item) {
      this.$emit('onAdd', item)
    },
    count(item) {
      this.$emit('onCount', item)
    }
  }
}
</script>

<style lang="scss">
  .v-table {
    width: 100%;
    border: 0; 
    border-collapse: collapse;
    &__top {
      margin-bottom: 1rem;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
    }
    &__search {
      width: 15rem;
      padding: .5rem 1rem;
      &::placeholder {
        font-size: .9rem;
      }
    }
    &__title {
      font-size: 2rem;
      text-align: left;
    }
    &__row {
      border-bottom: 1px solid #ccc; 
      &:hover {
        background-color: #ccc;
      }
      td{
        padding: 1rem;
        text-align: left;
      }
    }
    &__head, &__head:hover {
      background-color: rgb(51, 51, 51);
      color: #fff;
    }
    &__operations {
      i {
        font-size: 1.8rem;
        margin-right: .4rem;
        cursor: pointer;
      }
      .count {
        &:hover {
          color: green;
        }
      }
      .delete {
        &:hover {
          color: red;
        }
      }
      .add {
        &:hover {
          color: green;
        }
      }
    }
  }
</style>
