<template>
  <div class="add-to-storage">
    <vTable
      :caption="table.caption"
      :columns="table.columns"
      :data="dishItems"
      :operations="table.operations"
      @onAdd="addDishItem"
    />
  </div>
</template>

<script>
import {mapActions} from 'vuex'

import vTable from '@/components/table/v-table'
import vTableColumn from '@/components/table/v-table-column'

import {dateNow} from '@/helpers'

export default {
  name: 'AddToStorage',
  components: {
    vTable,
    vTableColumn
  },

  data: () => ({
    table: {
      caption: 'Добавить на склад',
      operations: {
        add: true
      },
      columns: [
        'Название',
        'Состав',
        'Себестоимость',
        'Цена продажи',
      ]
    }
  }),
  
  computed: {
    dishItems() {
      const dishItems = this.$store.getters.DISH_ITEMS.map(i => {
          const item = {...i}
          item.products = item.products.map(p => p.name).join(', ')
          return item
      })
      return dishItems
    }
  },
  
  methods: {
    ...mapActions(['ADD_STORAGE_ITEM']),
    addDishItem(item) {
      const quantity = prompt('Укажите количество:')

      let dishItem = {
        id: Date.now(),
        date: dateNow(),
        name: item.name,
        quantity: +quantity,
        costPrice: item.costPrice,
        revenue: item.price
      }
      this.ADD_STORAGE_ITEM(dishItem)
    }
  }
}
</script>

