import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    storage: [
      { id: 1, date: '2021-02-03', name: 'Роллы "Филадельфия"', quantity: 70, costPrice: 55000, revenue: 76000 },
      { id: 2, date: '2021-02-04', name: 'Роллы "Калифорния"', quantity: 30, costPrice: 53000, revenue: 736000 },
      { id: 3, date: '2021-02-05', name: 'Роллы "Темпура"', quantity: 40, costPrice: 15000, revenue: 7000 },
      { id: 4, date: '2021-02-06', name: 'Роллы "Унаги Ролл"', quantity: 50, costPrice: 535000, revenue: 71000 },
      { id: 5, date: '2021-02-06', name: 'Роллы "Инари"', quantity: 50, costPrice: 535000, revenue: 71000 },
      { id: 6, date: '2021-02-06', name: 'Роллы «Радуга»', quantity: 50, costPrice: 535000, revenue: 71000 }
    ],
    products: [
      { id: 1, name: 'Васаби', weight: 100, costPrice: 44},
      { id: 2, name: 'Имбирь', weight: 100, costPrice: 23},
      { id: 3, name: 'Нори', weight: 100, costPrice: 17},
      { id: 4, name: 'Рис', weight: 100, costPrice: 56}
    ],
    dishItems: [
      {
        id: 1,
        name: 'test', 
        products: [
          { id: 1, name: 'Васаби', weight: 100, costPrice: 44},
          { id: 2, name: 'Имбирь', weight: 100, costPrice: 23},
          { id: 3, name: 'Нори', weight: 100, costPrice: 17},
          { id: 4, name: 'Рис', weight: 100, costPrice: 56}
        ],
        costPrice: 111, 
        price: 333, 
      }
    ],
    sales: [
      {id: 1, date: '20.03.2021', name: 'Роллы "Филадельфия"', quantity: 21, proceeds: 3333}
    ],
    
  },
  mutations: {
    REMOVE_FROM_STORAGE(state, id) {
      state.storage = state.storage.filter(i => i.id !== id)
    },
    ADD_TO_STORAGE(state, item) {
      state.storage.push(item)
    },
    CHANGE_TO_STORAGE(state, item) {
      state.storage = state.storage.map(i => {
        if (i.id === item.id) {
          return item
        }
        return i
      })
    },
    REMOVE_FROM_PRODUCTS(state, id) {
      state.products = state.products.filter(i => i.id !== id)
    },
    ADD_TO_PRODUCTS(state, product) {
      state.products.push(product)
    },
    ADD_TO_DISH_ITEMS(state, dish) {
      state.dishItems.push(dish)
    },
    ADD_TO_SALES(state, item) {
      state.sales.push(item)
    }
  },
  actions: {
    DELETE_STORAGE_ITEM({commit}, id) {
      commit('REMOVE_FROM_STORAGE', id)
    },
    ADD_STORAGE_ITEM({commit}, item) {
      commit('ADD_TO_STORAGE', item)
    },
    CHANGE_STORAGE_ITEM({commit}, item) {
      commit('CHANGE_TO_STORAGE', item)
    },
    DELETE_PRODUCT_ITEM({commit}, id) {
      commit('REMOVE_FROM_PRODUCTS', id)
    },
    ADD_PRODUCT_ITEM({commit}, product) {
      commit('ADD_TO_PRODUCTS', product)
    },
    ADD_DISH_ITEM({commit}, dish) {
      commit('ADD_TO_DISH_ITEMS', dish)
    },
    ADD_SALE_ITEM({commit}, item) {
      commit('ADD_TO_SALES', item)
    }
  },
  getters: {
    STORAGE({storage}) {
      return storage
    },
    PRODUCTS({products}) {
      return products
    },
    DISH_ITEMS({dishItems}) {
      return dishItems
    },
    SALES({sales}) {
      return sales
    }
  }
})
